document.addEventListener("DOMContentLoaded", () => {
  app();
});

function app() {
  btn();
  arrowUp();
  navSroll();
}

function btn() {
  const btn = document.querySelector(".button");
  const nav__ul = document.querySelector(".nav__ul");
  const body = document.querySelector("#body");

  btn.addEventListener("click", () => {
    nav__ul.classList.toggle("activeBtn");
    btn.classList.toggle("activeBtn");
    body.classList.toggle("desactivarScroll");
  });
}

function navSroll() {
  const nav__ul = document.querySelector(".nav__ul");
  const btn = document.querySelector(".button");
  const body = document.querySelector("#body");
  const li_nav1 = document.querySelector(".li_nav1");
  const li_nav2 = document.querySelector(".li_nav2");
  const li_nav3 = document.querySelector(".li_nav3");
  const li_nav4 = document.querySelector(".li_nav4");
  const li_nav5 = document.querySelector(".li_nav5");

  li_nav1.addEventListener("click", () => {
    nav__ul.classList.remove("activeBtn");
    btn.classList.remove("activeBtn");
    body.classList.remove("desactivarScroll");
  });
  li_nav2.addEventListener("click", () => {
    nav__ul.classList.remove("activeBtn");
    btn.classList.remove("activeBtn");
    body.classList.remove("desactivarScroll");
  });
  li_nav3.addEventListener("click", () => {
    nav__ul.classList.remove("activeBtn");
    btn.classList.remove("activeBtn");
    body.classList.remove("desactivarScroll");
  });
  li_nav4.addEventListener("click", () => {
    nav__ul.classList.remove("activeBtn");
    btn.classList.remove("activeBtn");
    body.classList.remove("desactivarScroll");
  });
  li_nav5.addEventListener("click", () => {
    nav__ul.classList.remove("activeBtn");
    btn.classList.remove("activeBtn");
    body.classList.remove("desactivarScroll");
  });
}

function arrowUp() {
  const hero__botton = document.querySelector("#hero__botton");
  const arrowUp = document.querySelector(".arrow-up");

  window.addEventListener("scroll", () => {
    if (hero__botton.getBoundingClientRect().top < 0) {
      arrowUp.classList.add("active__arrowUp");
    } else {
      arrowUp.classList.remove("active__arrowUp");
    }
  });
}
